<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :storeMutationKey="storeMutationKey"
      :getDataCb="getDataCb"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewDevices, item)"></text-button>
      </template>
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <rf-network-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></rf-network-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <rf-network-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></rf-network-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import RfNetworkForm from './rfNetworkForm.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfNetworkService } from '../../../services/rfNetworkService';
import IconButton from '../../Common/IconButton.vue';

const dialogNames = {
  viewDevices: 'viewDevices',
};

const redirectMap = {
  [dialogNames.viewDevices]: networkId => ROUTES.rfDevices.replace(':networkId', networkId),
};

export default {
  name: 'rf-network-grid',
  components: { GenericGrid, RfNetworkForm, IconButton, TextButton },
  props: ['params', 'getDataCb'],
  data() {
    return {
      service: rfNetworkService,
      toolbarTitle: 'Network',
      storeMutationKey: 'changeRfNetworks',
      dialogNames: dialogNames,
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'routeLinkName',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  methods: {
    redirectTo(viewName, item) {
      const uuid = item.uuid;
      if (!uuid) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(uuid) });
      }
    },
  },
};
</script>
